<template>
	<div class="wrapper">
		<div class="header">
			<!-- <el-header style="height: initial;"> -->
			<cHeader />
		</div>
		<div class="main">
			<div class="sidebar">
				<cSidebar />
			</div>
			<div class="main_body">
				<div class="main_tag">
					<cTags />
				</div>
				<div class="main_content">
					<transition name="move" mode="out-in">
						<keep-alive :include="tagsList">
							<router-view></router-view>
						</keep-alive>
					</transition>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import home from "./home.js";
export default home;
</script>

<style>
@import url("home.css");
</style>



